import network from './network';


// 
export function login(data){
    return network({
        url:`/login`,
        method: "post",
        data
    });
}

// 

export function register(data){
    return network({
        url:`/register`,
        method:"post",
        data
    })
}

// 
export function queryTaskList(params){
    return network({
        url:`/queryTaskList/`,
        method:"get",
        params
    })
}


// 
export function editContent(data){
    return network({
        url:`/editContent`,
        method:"put",
        data
    })
}



