import {saveFile} from '../utils';
import defaultTools from '../config/tools'

export default {
    name: 'markdown',
    props: {
        value: {
            type: [String, Number],
            default: ''
        },
        theme:{// 
            type: String,
            default: 'light'
        },
        width:{ // 
            type: [Number, String],
            default:'auto'
        },
        height:{ // 
            type: Number,
            default: 600
        },
        toolbars:{ // 
            type: Object,
            default(){
                return {};
            }
        },
        bordered: {//
            type: Boolean,
            default: true
        },
        autoSave: {// 
            type: Boolean,
            default: false
        },
        interval:{//  
            type: Number,
            default: 12000
        },
        exportFileName:{// 
            type: String,
            default:'markdown'
        },
        compilerOptions:{// 
            type: Object,
            default(){
                return {};
            }
        },
        copyCode: {// 
            type: Boolean,
            default: true
        },
        copyBtnText: {// 
            type: String,
            default: ''
        },
        isPreview: {//
            type: Boolean,
            default: false
        }
    },
    data(){
        return{
            currentValue: '', //
            timeoutId: null,
            indexLenth: 1,
            html: '', // html
            preview: false, // 
            split: true, // 
            fullscreen: false, // 
            scrollSide: '', // 
            lastInsert: '', // 
            timerId: null, //  id
            themeName:'',
            themeSlideDown:false,
            imgSlideDown:false,
            imgs: [],
            scrolling: true, // 
            editorScrollHeight: 0,
            previewImgModal: false,
            previewImgSrc: '',
            previewImgMode: ''
        };
    },
    computed:{
        tools(){
            const {toolbars = {}} = this;
            return {
                ...defaultTools,
                ...toolbars
            }
        }
    },
    methods:{
        insertLink(){ // 
            this.insertContent('\n[link](href)');
        },
        insertImage(){ // 
            this.insertContent('\n![image](imgUrl)')
        },
        insertTable(){ // 
            this.insertContent(
                '\n| header 1 | header 2 | \n| ---|--- |\n| row 1 col 1 | row 1 col 2 |\n| row 2 col 1 | row 2 col 2 |\n\n'
                );
        },
        handleSave(){ // 
            const {currentValue, themeName, html} = this;
            this.$emit('on-save', {
                theme: themeName,
                value:currentValue,
                html
            });
        },
        toggleSlideDown(){ // 
            this.slideDown = !this.slideDown;
        },
        setThemes(name){ //  
            this.themeName = name;
            this.themeSlideDown = false;
            this.$emit('on-theme-change', name);
        },
        exportFile(){ //.md
            saveFile(this.currentValue, this.exportFileName + '.md');
        },
        importFile(e){ //
            // console.log(e)
            const file = e.target.files[0];
            console.log(file)
            if(!(file)){
                console.log('')
                return;
            }
            const {name} = file;
            // console.log(name)
            const fileExt = name.substring(name.lastIndexOf("."),name.length).toLowerCase()
            if(fileExt!='.md'){
                console.log('')
                return;
            }
            const reader = new FileReader();
            reader.readAsText(file, {
                encoding:'utf-8'
            });
            reader.onload = () => {
                this.currentValue = reader.result;
                e.target.value = '';
                this.editor.setOption('value', this.currentValue);
                console.log(" "+name)
            };
            reader.onerror = err => {
                console.error(err);
            }
        },
        handlePaste(_,e){ //
            const {clipboardData = {}} = e;
            const {types = [], items} =clipboardData;
            let item = null;
            for (let i = 0; i< types.length; i++){
                if (types[i] === 'Files'){
                    item = items[i]
                    break;
                }
            }
            if (item){
                const file = item.getAsFile();
                if(/image/gi.test(file.type)) {
                    this.$emit('on-upload-image',file);
                    e.preventDefault();
                }
            }
        },
        mousescrollSide(side){ // 
            this.scrollSide = side;
        },
        addImageClickListener(){ // 
            const {imgs = []} = this;
            if (imgs.length > 0){
                for (let i =0, len =imgs.length; i< len; i++){
                    imgs[i].onclick = null;
                }
            }
            setTimeout(() => {
                this.imgs = this.$refs.preview.querySelectorAll('img');
                for (let i = 0, len = this.imgs.length; i<len;i++){
                    this.imgs[i].onclick = () => {
                        const src = this.imgs[i].getAttribute('src');
                        this.previewImage(src);
                    };
                }
            }, 600);
        
        },
        previewImage(src) {// 
            const img = new Image();
            img.src = src;
            img.onload = () => {
                const width = img.naturalWidth;
                const height = img.naturalHeight;
                if (height / width > 1.4) {
                    this.previewImgMode = 'horizontal';
                } else {
                    this.previewImgMode = 'vertical';
                }
                this.previewImgSrc = src;
                this.previewImgModal = true;
            };
        },
        chooseImage(){ // 
            const input = document.createElement('input');
            input.type = 'file';
            input.accept = 'image/*';
            input.onchange = () =>{
                const files = input.files;
                if(files[0]){
                    this.$emit('on-upload-image', files[0]);
                    input.value = '';
                }
            }
            input.click();
        },
        addCopyListener() { // 
            setTimeout(() =>{
                const btns = document.querySelectorAll(
                    '.code-block .copy-code'
                );
                this.btns = btns;
                for (let i = 0; i < btns.length; i++){
                    btns[i].onclick = ()=>{
                        const code = btns[i].parentNode.querySelectorAll('pre')[0].innerText;
                        const aux = document.createElement('input');
                        aux.setAttribute('value', code);
                        document.body.appendChild(aux);
                        aux.select();
                        document.execCommand('copy');
                        document.body.removeChild(aux);
                        this.$emit('on-copy', code);
                    };
                }
            }, 600);
        }
    },
    destroyed() {//
        clearInterval(this.timerId);
    }

};

